//引用mSearch组件，如不需要删除即可
import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
import uniIcon from "@/components/uni-icon/uni-icon.vue"
import goodsList from "../../components/goods-list/goods-list.vue"
export default {
	data() {
		return {
			news:[],
			defaultKeyword: "",
			keyword: "",
			oldKeywordList: [],
			hotKeywordList: [],
			keywordList: [],
			forbid: '',
			isShowKeywordList: false,
			show:true,
			isdelete:false,
			DeleteIcon:true,
			isShowKeyword:true
		}
	},
	onLoad() {
		this.init();
		uni.request({
			url: 'http://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data;
				uni.hideLoading();//关闭加载
				
			},
			fail: () => {},
			complete: () => {}
		});
	},
	components: {
		//引用mSearch组件，如不需要删除即可
		mSearch,
		uniIcon,
		goodsList
	},
	methods: {
		init() {
			this.loadDefaultKeyword();
			this.loadOldKeyword();
			this.loadHotKeyword();

		},
		
		blur(){
			uni.hideKeyboard()
		},
		//加载默认搜索关键字
		loadDefaultKeyword() {
			//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
			this.defaultKeyword = "商户 | 产品 | 场景 | 地址";
		},
		//加载历史搜索,自动读取本地Storage
		loadOldKeyword() {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					var OldKeys = JSON.parse(res.data);
					this.oldKeywordList = OldKeys;
				}
			});
		},
		//加载热门搜索
		loadHotKeyword() {
			//定义热门搜索关键字，可以自己实现ajax请求数据再赋值
			this.hotKeywordList = ['温泉山庄', '温泉山庄', '温泉山庄', '温泉山庄', '蓝牙音箱', '笔记本电脑', '鼠标垫', 'USB', 'USB3.0'];
		},
		//监听输入
		inputChange(event) {
			//兼容引入组件时传入参数情况
			
			var keyword = event.detail?event.detail.value:event;
			if (!keyword) {
				this.keywordList = [];
				this.isShowKeywordList = false;
				return;
			}
			this.isShowKeywordList = true;
			//以下示例截取淘宝的关键字，请替换成你的接口
			uni.request({
				url: 'https://suggest.taobao.com/sug?code=utf-8&q=' + keyword, //仅为示例
				success: (res) => {
					this.keywordList = this.drawCorrelativeKeyword(res.data.result, keyword);
				}
			});
		},
		//高亮关键字
		drawCorrelativeKeyword(keywords, keyword) {
			var len = keywords.length,
				keywordArr = [];
			for (var i = 0; i < len; i++) {
				var row = keywords[i];
				//定义高亮#9f9f9f
				var html = row[0].replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
				html = '<div>' + html + '</div>';
				var tmpObj = {
					keyword: row[0],
					htmlStr: html
				};
				keywordArr.push(tmpObj)
			}
			return keywordArr;
		},
		//顶置关键字
		setkeyword(data) {
			this.keyword = data.keyword;
		},
		//点击删除图标显示每个搜索历史后的小删除按钮，且将删除图标替换为文字“全部删除|完成”
		oldDelete() {
			this.isdelete = true;
			this.DeleteIcon = false;
		},
		//点击全部删除将搜索历史部分隐藏
		allDelete(){
			this.oldKeywordList = [];
			uni.removeStorage({
				key: 'OldKeys'
			});
		},
		//点击完成，隐藏每个删除按钮，显示搜索图标
		deleteSuccess(){
			this.isdelete = false;
			this.DeleteIcon = true;
		},
		//点击某个搜索历史后的搜索小图标删除这一项
		deleteThis(keyword,index){
			this.oldKeywordList.splice(index,1);
		},
		//热门搜索开关
		hotToggle() {
			this.forbid = this.forbid ? '' : '_forbid';
		},
		//执行搜索
		doSearch(key) {
			key = key ? key : this.keyword ? this.keyword : this.defaultKeyword;
			
			this.keyword = key;
			this.saveKeyword(key); //保存为历史 
			this.isShowKeywordList = false;
			this.show = false;
			console.log(key)
			if(key=="商户"||key=="产品"||key=="场景"||key=="地址"){
				// 跳转至搜索列表
				uni.navigateTo({
					url:"searchlistPage"
				})
			}

			//以下是示例跳转淘宝搜索，可自己实现搜索逻辑






		},
		//保存关键字到历史记录
		saveKeyword(keyword) {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					console.log(res.data);
					var OldKeys = JSON.parse(res.data);
					var findIndex = OldKeys.indexOf(keyword);
					if (findIndex == -1) {
						OldKeys.unshift(keyword);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(keyword);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				},
				fail: (e) => {
					var OldKeys = [keyword];
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				}
			});
		},
		//点击搜索框默认显示删除图标隐藏每个搜索历史的删除按钮
		getFocus(){
			this.DeleteIcon = true;
			this.isdelete = false;
			
		},
		//点击取消图标自动显示搜索历史
		cancelIcon(){
			this.show = true;
		},
	    goGoodsDetails(e){
	    	console.log(e)
	    	uni.navigateTo({
	    		url:'../detailPage/detailPage',
	    		success:function(){
	    			console.log("成功跳转")
	    		}
	    	})
	    },
		goMapPage(){
			console.log("lalall")
			uni.navigateBack()
		},
	}
}
